.tab-content
    opacity: 0
    visibility: hidden
    transition: opacity 0.5s ease-in-out, visibility 0s linear 0.5s

.tab-content.active
    opacity: 1
    visibility: visible
    transition-delay: 0s

.tab-button
    position: relative
    &::after
        content: ''
        position: absolute
        left: 0
        right: 0
        bottom: -14px
        height: 2px
        background-color: #3E6C8A
        opacity: 0
        transition: opacity 0.3s ease
    &.active::after
        opacity: 1

.section-intro
    &--description
        &::after
            content: ""
            display: block
            width: 145px
            height: 70px
            position: absolute
            bottom: 0
            right: -40px
            background-image: url('/wp-content/themes/aare/assets/images/waves.svg')
            background-position: bottom right
            background-repeat: no-repeat
            background-size: contain
            z-index: -1
    &--description-2
        &::after
            content: ""
            display: block
            width: 70px
            height: 145px
            position: absolute
            top: -20px
            right: 0px
            background-image: url('/wp-content/themes/aare/assets/images/waves-vertical.svg')
            background-position: bottom right
            background-repeat: no-repeat
            background-size: contain
            z-index: -1

@media (min-width:1024px)
    .after-vertical-line
        &::after
            content: ""
            position: absolute
            top: 0
            right: -13px
            height: 100%
            width: 1px
            background-color: #3E6C8A

@media (max-width:1023px)
    .after-vertical-line
        &::after
            content: ""
            position: absolute
            bottom: 0
            right: 0
            height: 1px
            width: 100%
            background-color: #3E6C8A